

.navBarContainer .nav-item{
    text-decoration: none;
   

}

.navBarContainer .nav-item :hover{ 
    
    background-color: #a9d63b;
    border-radius: 25px;
       
   
}
.navBarContainer .nav-item:hover .nav-link{
    color: white;
}

.navBarContainer #check{
    display: none;

}
.navBarContainer .nav-link {
    padding: 10px;
    margin: 10px;
    text-decoration: none;    
    font-size: 17px;
    text-transform: uppercase;
    font-weight: 400;
    font-weight: bold;
    color: #f57f25;
    
    
    
}

.navBarContainer .sideBar {
    text-align: left;  
    display: none;
    float: right;
    
   

}

@media only screen and (max-width: 992px) {
    .navBarContainer .nav{
        display: none;
        text-align: center;
        top:0px; 
        left:0px;
        transition: ease .5s; 
      
      
    }
   
    .navBarContainer .sideBar{
        display: block;
        padding:5%;
    }

    .navBarContainer .nav-item :hover{ 
        
        border-radius: 0px;
    
       
    }
    .navBarContainer img{
        width: 60vw;
        
    }

  
}
    

