.galleryContainer{
    margin-top: 20px;
    margin-bottom: 20px;
    max-width: 100%;
}

.galleryContainer .container{
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto
}
.galleryContainer .row{
    margin-right: -15px;
    margin-left: -15px;
    
}
.galleryContainer figure {
    margin: 0;
    position: relative;
}
.galleryContainer figure img{
    width: 100%;
    vertical-align: middle;
   border: 0;
    
}
.galleryContainer .imageContainer{
  margin-top: 30px;
  padding: 10px;
  
}