.sliderContent{
    background:linear-gradient( rgba(0, 0, 0, 0.6) 100%, rgba(0, 0, 0, 0.6)100%),url('../images/building-tower.jpg');
    background-size:cover;
      
}

.slidertext{
    text-align: center;
    padding:0;
    margin: 15%;
}

@keyframes h1animation{
    0%{
        transform: translateY(-40px);
    }
    20%{
       transform: translateY(0px);
    }

    100%{
        transform: translateY(0px);  
         
    }
}
.slidertext h1{
    position: relative;
    color: hsl(26, 91%, 55%);
    font-size:5vw;
    font-weight: 400;
    line-height: 1.2em;
    word-spacing: 2px;
    font-family: 'Lobster', cursive;
    text-shadow: 3px 0 0 #fff, -2px 0 0 #fff, 0 3px 0 #fff, 0 -2px 0 #fff, 2px 2px #fff, -1px -1px 0 #fff, 2px -1px 0 #fff, -1px 2px 0 #fff;
    animation-name:h1animation;
    animation-duration:20s;
    animation-timing-function:ease;
    animation-iteration-count: infinite;
   


}
@media only screen and (max-width: 992px){
    .slidertext h1{
    font-size: 10vw;
    }
}

@keyframes h2animation{
    0%{
        transform: translateX(-50px);
    }
    20%{
       transform: translateX(0px);
    }

    100%{
        transform: translateX(0px);  
         
    }
}
.slidertext h2{
    color: white;
    font-size: 1.6rem;
    font-weight: 600px;
    line-height: 1.6em;
    font-family: 'Open Sans', sans-serif;
    animation-name:h2animation;
    animation-duration:20s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
 
}

@keyframes panimation{
    0%{
       transform: translateX(50px);
    }
    20%{
        transform: translateX(0px);   
    }
    100%{
        transform: translateX(0px);   
    }
}

.slidertext p{
    color: white;
    font-size: 1rem;
    animation-name:panimation;
    animation-duration:20s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
    padding-bottom: 20px;

}


