.ServicesCardContainer{
    padding-top: 40px;
    padding-bottom: 40px;
    
}   

.ServicesCardContainer .container-fluid{
    padding-left: 20px;
    padding-right: 20px;
    margin-right: auto;
    padding-right: auto;
}
.ServicesCardContainer .row {
    margin-right: -15px;
    margin-left: -15px;
}
.ServicesCardContainer .card-image{
    float: left;
    width: 20%;
    
    padding-top: 20px;
    color: white;
    font-size: 50px;
}
.ServicesCardContainer .card-body{
    float: right;
    width: 75%;
    color: #fff;
    
}


.ServicesCardContainer .card-title{
    font-weight: 60;
    line-height: 1.6em;
    font-family: 'Open Sans', sans-serif;
}
.ServicesCardContainer .card-text{
    font-family: 'Open Sans', sans-serif;
    font-size: 15px;
    line-height: 24px;
}
.ServicesCardContainer .cardContainer {
   
     float: left;
     
     box-shadow:4px 5px 5px 4px lightgray;
    padding: 25px;
    margin:20px;
}
