.EnrollNowContainer{
    width: 100%;
    background-color: #9321eb;
    padding:95px 0px 100px 0px;
        
}
.EnrollNowContainer h3{
    position: relative;
    display: block;
    font-size: 1.7rem;
    line-height: 30px;
    color: #fff;
    font-weight: 700;
    margin-bottom: 11px;
}
.EnrollNowContainer h1{
    position: relative;
    display: block;
    font-size: 3rem;
    line-height: 70px;
    color: #fff;
    font-weight: 700;
    margin-bottom: 11px;
}
.EnrollNowContainer p{
    position: relative;
    display: block;
    font-size: 1.1rem;
    line-height: 30px;
    color: #dfbdf9;
    font-weight: 600;
    margin-bottom: 36px;
    
}
.EnrollNowContainer .button-body{
    background-color: #ff7162;
    text-transform: uppercase;
    padding: 17px 40px;
    color: white;
    text-decoration: none;
    font-size:1rem;
    font-weight: 900;
    
}