.teacherContainer{
    padding-top: 40px;
    padding-bottom: 40px;
}
.teacherContainer h1{
    padding-top: 20px;
    padding-bottom: 20px;
    font-size: 2.5rem;
    font-weight: 800;
    font-family: 'Lobster', cursive;
    color: #f57f25;

}
.teacherContainer .inner-box{
    padding-top: 20px;
       
}
.teacherContainer .image-holder{
    position: relative;
    display: inline-block;
    border: 2px dashed #ffba06;
    padding: 18px;
    border-radius: 50%;
    
}
.teacherContainer .image-box{
    background-color:lightslategrey;
    border-radius: 50%;
}

.teacherContainer .lower-content{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -30px;
    width: 100%;
   
}
.teacherContainer h3{
    background-color: #007bff;
    border-radius: 25px;
    color: white;
    padding: 20px;
    width:300px;   
    

}
.teacherContainer .image-holder :hover{
    opacity: 0.9;
  
}
.teacherContainer .image-holder:hover > .image-box img{
     transform: scale(1.02); 
}

